:root {
  --background: #ffffff;
  --foreground: #171717;
}
@media (prefers-color-scheme: dark) {
  :root {
    --background: #0a0a0a;
    --foreground: #ededed;
  }
}
html,
body {
  height: 100%;
}
body {
  background: var(--background);
  color: var(--foreground);
  font-family: Arial, Helvetica, sans-serif;
  overflow-x: hidden;
}
.phone-input {
  display: flex;
  align-items: center;
  gap: 8px;
}
.phone-input input {
  background: transparent;
  border: none;
  outline: none;
  width: 100%;
  font-size: 14px;
  color: #0f172a;
}
.phone-input input::placeholder {
  color: #9ca3af;
}
.phone-input .PhoneInputCountrySelect {
  border: none;
  background: transparent;
  outline: none;
  cursor: pointer;
}
.phone-input .PhoneInputCountryIcon {
  width: 20px;
  height: 20px;
}
